  display: flex;
}

.LDFIVW_nav-bar img {
  aspect-ratio: 1;
  width: 50px;
}

.LDFIVW_todo-wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: min(730px, 95%);
  height: 100%;
  min-height: 250px;
  margin: 0 auto;
  display: flex;
}

.LDFIVW_todo-card {
  background: #fefefe;
  border-radius: 2rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 600px;
  min-height: 660px;
  margin: 50px 0;
  padding: 70px 80px;
  display: flex;
